	module.exports = {"data":{"markdownRemark":{"html":"<h2>Width Property</h2>\n<p>The width property sets the width of an element. It can be a keyword value, a length value (any value followed by a CSS unit like px or em), percentage, or it can be inherited by it’s parent. By default, it’s value is auto.</p>\n<p><strong>Note</strong>: The width property does not include padding, borders, or margins; it sets the width of the area inside the padding, border, and margin of the element!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token selector\">.length-value</span> <span class=\"token punctuation\">{</span><span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 100px<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.percentage</span> <span class=\"token punctuation\">{</span><span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 50%<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.auto</span> <span class=\"token punctuation\">{</span><span class=\"token property\">width</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inherit</span> <span class=\"token punctuation\">{</span><span class=\"token property\">width</span><span class=\"token punctuation\">:</span> inherit<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.initial</span> <span class=\"token punctuation\">{</span><span class=\"token property\">width</span><span class=\"token punctuation\">:</span> initial<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><strong>Note</strong>: The min-width and max-width properties override width.</p>\n<h4>More Information:</h4>\n<p>Docs: <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/width' target='_blank' rel='nofollow'>MDN</a></p>\n<p>Browser Support: It’s categorised under the ‘well supported sub-set’, so need not worry for browser support</p>","fields":{"slug":"/css/properties/width-property/"},"frontmatter":{"title":"Width Property"}}},"pathContext":{"slug":"/css/properties/width-property/"}}

